.hourglass {
  margin: 20px auto;
  height: 600px;
  width: 300px;
  animation: flip 10s ease infinite; }

.top, .bottom {
  background-color: rgba(239, 236, 202, 0.15);
  background-image: linear-gradient(#EFECCA, #EFECCA);
  background-size: 300px 300px;
  background-repeat: no-repeat;
  width: 300px;
  height: 300px; }

.top {
  background-position: 0 0;
  animation: top-do 10s ease infinite;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 50% 100%); }

.bottom {
  background-position: 0 300px;
  animation: bottom-do 10s ease infinite;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%); }
  .bottom:after {
    content: "";
    position: absolute;
    width: 2px;
    height: 300px;
    left: 149px;
    background-image: linear-gradient(#EFECCA, transparent); }

@keyframes top-do {
  95%, 100% {
    background-position: 0 300px; } }

@keyframes bottom-do {
  95%, 100% {
    background-position: 0 0; } }

@keyframes flip {
  0%, 95% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(180deg); } }

