  .hamburger-icon__span {
    display: block;
    position: absolute;
    top: 10px;
    left: 0;
    right: 0;
    height: 2px;
    background: #A1A3A5;
    transition: transform 0.3s; }
    .hamburger-icon__span::before, .hamburger-icon__span::after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #A1A3A5;
      content: ""; }
    .hamburger-icon__span::before {
      top: -6px;
      transform-origin: top right;
      transition: transform 0.3s, width 0.3s, top 0.3s; }
    .hamburger-icon__span::after {
      bottom: -6px;
      transform-origin: bottom right;
      transition: transform 0.3s, width 0.3s, bottom 0.3s; }
    .hamburger-icon__span.active {
      transform: rotate(180deg); }
    .hamburger-icon__span.active::before, .hamburger-icon__span.active::after {
      width: 50%; }
    .hamburger-icon__span.active::before {
      top: -3px;
      left: 7px;
      transform: translateX(6px) translateY(4px) rotate(45deg); }
    .hamburger-icon__span.active::after {
